	module.exports = "<h2 id=overview>Overview</h2> <p>A Chip is used to display items that have been filtered or selected from a larger group. They comprise of a text element and a button component that is used to remove the chip from selection. When the text overflows it is truncated using ellipses. A chip can be grouped by using the &quot;chip-group&quot; layout. </p> <h2 id=accessibility>Accessibility</h2> <table> <thead> <tr> <th>Attribute</th> <th>Applied To</th> <th>Outcome</th> </tr> </thead> <tbody> <tr> <td><code>aria-label=&quot;[button label text]&quot;</code></td> <td><code>.pf-c-button</code></td> <td>Provides an accessible name for the button when an icon is used instead of text. Required when an icon is used with no supporting text.</td> </tr> <tr> <td><code>aria-labelledby=&quot;[id value of .pf-c-button]&quot;</code></td> <td><code>.pf-c-button</code></td> <td>Gives the button an accessible name by referring to the element that provides the position of the button within a list. Required when the button is being removed.</td> </tr> <tr> <td><code>aria-hidden=&quot;true&quot;</code></td> <td><code>&lt;i&gt;</code></td> <td>Hides the icon from assistive technologies.</td> </tr> </tbody> </table> <h2 id=usage>Usage</h2> <table> <thead> <tr> <th>Class</th> <th>Applied To</th> <th>Outcome</th> </tr> </thead> <tbody> <tr> <td><code>.pf-c-chip</code></td> <td><code>&lt;div&gt;</code></td> <td>Initiates the body of a chip.</td> </tr> <tr> <td><code>.pf-c-chip__text</code></td> <td><code>&lt;span&gt;</code></td> <td>Initiates the text inside of the chip. <strong>Required.</strong></td> </tr> <tr> <td><code>.pf-c-button</code></td> <td><code>&lt;button&gt;</code></td> <td>Initiates the button used to remove the chip. <strong>Required.</strong></td> </tr> <tr> <td><code>.pf-m-overflow</code></td> <td><code>.pf-c-chip</code></td> <td>Applies styling of the overflow chip.</td> </tr> </tbody> </table> ";

